<template>
  <section>
    <el-col span="24" class="toolbar" style="margin-top: 10px;">
      <el-form :inline="true" size="mini">
        <el-col span = "20">
          <el-form-item>
            <el-input v-model="searchContent"  @keyup.enter.native="startSearch()" size="mini" suffixIcon="el-icon-search" style="width: 200px; font-size: 9px" :maxlength="140" placeholder="车牌/车型/姓名/手机/车架号/发动机号"></el-input>
          </el-form-item>
        </el-col>
        <el-form-item>
          <el-button>新增</el-button>
        </el-form-item>
      </el-form>

    </el-col>

    <el-table :data="orders" highlight-current-row v-loading="listLoading" @selection-change="selsChange" border
              stripe
              style="width: 100%; font-size: 10px">
      <el-table-column prop="id" label="序号" width="100" sortable></el-table-column>
      <el-table-column prop="vip" label="会员" width="100"></el-table-column>
      <el-table-column prop="carBrand" label="车品牌" width="100"></el-table-column>
      <el-table-column prop="carNo" label="车牌" width="100"></el-table-column>
      <el-table-column prop="carType" label="车型" width="100"></el-table-column>
      <el-table-column prop="userName" label="姓名" width="100"></el-table-column>
      <el-table-column prop="manager" label="手机" width="100"></el-table-column>
      <el-table-column prop="atWeichat" label="关注微信" width="100"></el-table-column>
      <el-table-column prop="manager" label="客户经理" width="100"></el-table-column>
      <el-table-column  label="操作" width="100">  <template scope="scope"> <el-button style="font-size: 8px; padding: 3px" @click="handleEdit(scope.$index, scope.row)">查看</el-button></template></el-table-column>
    </el-table>
  </section>
</template>

<script>
  import ElButton from "../../../node_modules/element-ui/packages/button/src/button.vue";
  import ElCol from "element-ui/packages/col/src/col";

  export default {
    components: {
      ElCol,
      ElButton},
    methods:{
      startSearch(){
        if(this.searchContent == "")
          alert("请输入搜索内容")
        else{
          alert("没有更多了")
        }
      }
    },
    data() {
      return {
        searchContent:"",
        orders: [
          {
            id: "1",
            vip: "",
            carBrand: "",
            carNo: "粤B43457",
            carType: "宝马x5",
            userName: "粤B43457",
            phone: "13456094039",
            atWeichat: "",
            manager: "",
            fromWhere: "移动端",
          },
          {
            id: "2",
            vip: "",
            carBrand: "",
            carNo: "粤B44658",
            carType: "奥迪Q7",
            userName: "李先生",
            phone: "",
            atWeichat: "",
            manager: "",
            fromWhere: "移动端",
          }
        ]
      }
    }
  }
</script>



